<template>
  <div class="data pt-3 pb-5">
    <div class="container">
      <div class="row">
        <div class="col">
          <router-link to="/">
            <img src="../assets/img/logo.png" width="235px" class="d-none d-md-block" alt="">
            <img src="../assets/img/logo.png" width="135px" class="d-md-none" alt="">
          </router-link>
        </div>
      </div>
      <div class="row mt-2">
        <div class="col-12 col-md-8">
          <p class="font-weight-bold h4 text-orange">{{data[dataID].title}}</p>
          <div class="text-gray mt-4">
            <img :src="data[dataID].img" class="float-left mr-2" width="180px" alt="">
            <p class="h6font-weight-bold text-first" v-html="data[dataID].abstract"></p>
            <br>
            <p v-if="data[dataID].include.length>=1" class="h4 font-weight-bold text-first">what's included??</p>
            <ul class="ml-4">
              <li v-for="(item,index) in data[dataID].include" :key="index" >{{item}}</li>
            </ul>
          </div>
        </div>
        <div class="d-none d-md-block col-md-4 mt-md-0 mt-4">
          <div class="border border-gray">
            <p class="p-3 h4 border-bottom border-gray text-first font-weight-bold">報告資訊</p>
            <p class="p-3 h6 border-bottom border-gray text-first">{{data[dataID].title}}</p>
            <div class="p-3 border-bottom border-gray text-first">
              <p class="h6 mb-2">頁數: {{data[dataID].pagecount}}</p>
              <p class="h6 mb-2">檔案類型: {{data[dataID].filetype}}</p>
            </div>
            <!-- <div class="text-first p-3 d-flex align-items-end justify-content-between border-bottom border-gray">
              <p>Price</p>
              <p class="h4 mb-0">{{data[dataID].price}}</p>
            </div> -->
            <div class="p-3 text-center bg-light-orange">
              <button class="order-btn py-2 px-3" @click="gotoSurvey()">聯絡我們</button>
            </div>
          </div>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col">
          <div class="d-flex border-bottom border-gray">
            <div class="px-3 py-2 cursor-pointer" @click="tabVal ='maincatalogue'" :class="{'tabItem':tabVal === 'maincatalogue'}">章節目錄</div>
            <div class="px-3 py-2 cursor-pointer" @click="tabVal = 'picturecatalogue'" :class="{'tabItem':tabVal === 'picturecatalogue'}">圖目錄</div>
            <div class="px-3 py-2 cursor-pointer" @click="tabVal = 'tablecatalogue'" :class="{'tabItem':tabVal === 'tablecatalogue'}">表目錄</div>
          </div>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col">
          <div v-if="tabVal === 'maincatalogue'">
            <div v-for="(item, index) in data[dataID].maincatalogue" :key="index" class="">
                <div class="d-flex justify-content-between align-items-end mb-2 text-first">
                  <span class="mr-2 text-nowrap">{{item.title}}</span>
                  <span class="float-right">{{item.pages}}</span>
                </div>
                <div v-if="item.content">
                  <div v-for="(i, index) in item.content" :key="index" class="ml-3 d-flex justify-content-between align-items-end mb-2 text-first">
                    <span class="mr-2 text-nowrap">{{i.title}}</span>
                    <span class="float-right">{{i.pages}}</span>
                  </div>
                </div>
            </div>
          </div>
          <div v-if="tabVal === 'picturecatalogue'">
            <div v-for="(item, index) in data[dataID].picturecatalogue" :key="index" class="d-flex justify-content-between align-items-end mb-2 text-first">
                <span class="mr-2 text-nowrap">{{item.title}}</span>
                <div class=" border-dotted w-100 mr-1"></div>
                <span class="">{{item.pages}}</span>
            </div>
          </div>
          <div v-if="tabVal === 'tablecatalogue'">
            <div v-for="(item, index) in data[dataID].tablecatalogue" :key="index" class="d-flex justify-content-between align-items-end mb-2 text-first">
                <span class="mr-2 text-nowrap">{{item.title}}</span>
                <div class=" border-dotted w-100 mr-1"></div>
                <span class="float-right">{{item.pages}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="row mt-3 d-md-none">
        <div class="col-md-4 mt-md-0 mt-4">
          <div class="border border-gray">
            <p class="p-3 h4 border-bottom border-gray text-first font-weight-bold">報告資訊</p>
            <p class="p-3 h6 border-bottom border-gray text-first">{{data[dataID].title}}</p>
            <div class="p-3 border-bottom border-gray text-first">
              <p class="h6 mb-2">頁數: {{data[dataID].pagecount}}</p>
              <p class="h6 mb-2">檔案類型: {{data[dataID].filetype}}</p>
            </div>
            <!-- <div class="text-first p-3 d-flex align-items-end justify-content-between border-bottom border-gray">
              <p>Price</p>
              <p class="h4 mb-0">{{data[dataID].price}}</p>
            </div> -->
            <div class="p-3 text-center bg-light-orange">
              <button class="order-btn py-2 px-3" @click="gotoSurvey()">聯絡我們</button>
            </div>
          </div>
        </div>
      </div>
      <div class="row mt-3 d-md-block d-none">
        <div class="col text-center">
          <button class="order-btn py-2 px-3" @click="gotoSurvey()">聯絡我們</button>
        </div>
      </div>
      <div class="row mt-4">
        <div class="col">
          <p class="h2 font-weight-bold">更多報告</p>
        </div>
      </div>
      <div class="row mt-1">
        <div class="col-12 col-md-6 col-lg-4 mb-3" v-for="(i,index) in moreArr" :key="index">
          <img :src="data[i].img" width="100%" alt="" class="cursor-pointer" @click="gotoIntroduce(i, data[i].title)">
          <p class="h4 text-orange cursor-pointer" @click="gotoIntroduce(i, data[i].title)">{{data[i].title}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import alldata from '../data/AllData'
export default {
  name: 'home',
  metaInfo: {
    title: 'introintro',
    titleTemplate: '%s | vue-meta Example App',
    htmlAttrs: {
      lang: 'en-US'
    },
    meta: [
      { charset: 'utf-8' },
      { name: 'description', content: 'introintro' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1' }
    ]
  },
  data () {
    return {
      dataID: 0,
      tabVal: 'maincatalogue',
      data: [],
      moreArr: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.dataID = Number(this.$route.query.id)
      this.data = alldata
      this.tabVal = 'maincatalogue'
      this.moreArr = []
      // console.log(this.$route.query.id)
      function getRandom (min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min
      }
      var mySet = new Set()
      // eslint-disable-next-line
      var w = 0
      if (this.data.length > 3) {
        do {
          w++
          let j = getRandom(0, this.data.length - 1)
          if (j !== this.dataID) {
            mySet.add(j)
          }
        } while (mySet.size !== 3)
        this.moreArr = mySet
        this.$nextTick(() => {
          window.scrollTo({
            top: 0,
            behavior: 'smooth'
          })
        })
      } else {
        for (let i = 0; i < this.data.length; i++) {
          if (i !== this.dataID) {
            this.moreArr.push(i)
          }
        }
      }
      // document.querySelector('title').innerText = this.data[this.dataID].seoshare.title
      // document.querySelector('head meta[name="description"]').content = this.data[this.dataID].seoshare.description
      // document.querySelector('head meta[property="og:title"]').content = this.data[this.dataID].seoshare.title
      // document.querySelector('head meta[property="og:description"]').content = this.data[this.dataID].seoshare.description
      console.log(document.querySelector('head meta[property="og:title"]'))
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    },
    gotoSurvey () {
      console.log(`Google 表單跳轉_${this.data[this.dataID].title}`)
      // eslint-disable-next-line
      // gtag('event', 'Page Click', { 
      //   'event_category': 'Click',
      //   'event_label': `連結跳轉_${this.data[this.dataID].title}`,
      //   'value': 1
      // })
      window.open(this.data[this.dataID].url)
    },
    gotoIntroduce (i, title) {
      console.log(i, title, `更多報告_點擊${title}`)
      // eslint-disable-next-line
      // gtag('event', 'Page Click', { 
      //   'event_category': 'Click',
      //   'event_label': `更多報告點擊_${title}`,
      //   'value': 1
      // })
      this.$router.push({name: 'Introduce', query: {id: i}})
      this.$nextTick(() => {
        this.init()
      })
    }
  }
}
</script>

<style>

</style>



