import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home.vue'
import Introduce from '@/views/Introduce.vue'
Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/introduce',
      name: 'Introduce',
      component: Introduce
    }
  ]
})



