<template>
  <div class="home mx-auto pb-5">
    <div class="container p-2">
      <div class="row">
        <div class="col">
          <router-link to="/">
            <img src="../assets/img/logo.png" width="235px" class="d-none d-md-block" alt="">
          </router-link>
          <router-link to="/">
            <img src="../assets/img/logo.png" width="135px" class="d-md-none" alt="">
          </router-link>
        </div>
      </div>
      <div class="row mt-2">
        <div class="col-12">
          <div class="banner">
            <swiper ref="swiperComponent" :options="swiperOptions">
            <swiper-slide v-for="(item, index) in bannerList" :key="index" >
                <div class="w-100 d-md-flex  banner-box p-1">
                  <img :src="item.banner" width="75%" class="d-md-block d-none cursor-pointer" @click="gotoIntroduce(item, index)" alt="">
                  <img :src="item.banner" width="100%" class="d-md-none cursor-pointer" @click="gotoIntroduce(item, index)" alt="">
                  <div class="pl-3 pr-2 pt-3 d-none d-md-block w-25 banner-card">
                    <p class="font-weight-bold h4 text-orange cursor-pointer word-break" @click="gotoIntroduce(item, index)">{{ item.title }}</p>
                    <p v-html="item.abstract.substring(0,35) + '...'" class="word-break h6"></p>
                    <p class="cursor-pointer text-gray h6" @click="gotoIntroduce(item, index)"><u>了解更多</u></p>
                  </div>
                </div>
            </swiper-slide>
              <div class="swiper-pagination"  slot="pagination"></div>
          </swiper>
          </div>
        </div>
      </div>
      <div class="row mt-5">
        <div class="col h1 font-weight-bold text-first">最新消息</div>
      </div>
      <div class="row align-items-end">
        <div class="col-12 col-md-6 col-lg-4 mb-3" v-for="(item,index) in dataList" :key="index">
          <p class="h4 text-orange">{{dataList[index].title}}</p>
          <div>
            <img :src="dataList[index].img" width="100%" alt="">
            <p v-html="dataList[index].date"></p>
            <p v-html="dataList[index].abstract.substring(0,30) + '...'"></p>
            <u @click="gotoIntroduce(item, index)" class="text-gray cursor-pointer">了解更多</u>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import alldata from '../data/AllData'
export default {
  name: 'home',
  metaInfo: {
    title: 'HOMEHOMEHOME',
    titleTemplate: '%s | vue-meta Example App',
    htmlAttrs: {
      lang: 'en-US'
    },
    meta: [
      { charset: 'utf-8' },
      { name: 'description', content: 'HOMEHOMEHOMEHOMEHOMEHOME' },
      { name: 'viewport', content: 'width=device-width, initial-scale=1' }
    ]
  },
  data () {
    return {
      dataList: [],
      bannerList: [],
      swiperOptions: {
        autoplay: true,
        loop: true,
        spaceBetween: 15,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  created () {
    this.dataList = alldata
    for (let i = 0; i < this.dataList.length; i++) {
      if (this.dataList[i].isHot) {
        this.bannerList.push({id: i, banner: this.dataList[i].banner, abstract: this.dataList[i].abstract, title: this.dataList[i].title})
      }
    }
    // document.querySelector('title').innerText = 'UUPON x 中大影響力管理與衡量研究中心'
    // document.querySelector('head meta[name="description"]').content = '2021市場調查研究： 串流影音及Podcast使用與消費行為調查、永續美妝保養品消費調查'
    // document.querySelector('head meta[property="og:title"]').content = 'UUPON x 中大影響力管理與衡量研究中心'
    // document.querySelector('head meta[property="og:description"]').content = '2021市場調查研究： 串流影音及Podcast使用與消費行為調查、永續美妝保養品消費調查'
  },
  computed: {
  },
  methods: {
    gotoIntroduce (item, index) {
      console.log(index, item.id, item.title)
      if (item.id) {
        // eslint-disable-next-line
        // gtag('event', 'Page Click', { 
        //   'event_category': 'Click',
        //   'event_label': `Banner_點擊${item.title}`,
        //   'value': 1
        // })
        console.log(`Banner_點擊${item.title}`)
        this.$router.push({name: 'Introduce', query: {id: item.id}})
      } else {
        // eslint-disable-next-line
        // gtag('event', 'Page Click', { 
        //   'event_category': 'Click',
        //   'event_label': `最新消息_點擊${item.title}`,
        //   'value': 1
        // })
        console.log(`最新消息_點擊${item.title}`)
        this.$router.push({name: 'Introduce', query: {id: index}})
      }
    }
  }

}
</script>

<style>

</style>



